.gen {
  position: relative;
  margin-top: 11rem;
  margin-bottom: 8rem;

  &__background {
    width: 128rem;
    display: block;
    position: absolute;
    left: 50%;
    margin-left: -65rem;
    top: 50%;
    margin-top: -36rem;
    opacity: 0.11;
    z-index: -1;

    @include respond(ipad-pro) {
      display: none;
    }
  }

  &__header {
    color: $color-white;
    font-family: Mosk-700;
    font-size: 6rem;
    text-align: center;
    line-height: 1;
    padding-top: 5rem;
    padding-bottom: 4rem;

    @include respond(big-desktop) {
      font-size: $font-size-90;
    }

    @include respond(ipad-pro) {
      margin-top: 10rem;
    }
  }

  &__text {
    color: $color-white;
    font-family: Mosk-300;
    font-size: 2.8rem;
    text-align: center;
    padding-bottom: 8rem;
  }

  &__buttons-container {
    padding-top: 5rem;
    text-align: center;
  }
}

.gen-btn {
  width: 22.8rem;
  height: 5.4rem;
  margin-right: 4rem;
  border-radius: 0.4rem;
  color: $color-white;
  cursor: pointer;

  &--1 {
    background: transparent
      linear-gradient(
        75deg,
        $color-teal 0%,
        $color-teal 47%,
        $color-pink 100%,
        $color-black 100%
      )
      0% 0% no-repeat padding-box;
    font-family: Mosk-700;
    font-size: 2.8rem;
    border: none;
  }

  &--2 {
    background: none;
    font-family: Mosk-400;
    font-size: $font-size-25;
    border: solid 1px $color-pink;
  }
}

/* Break element that will only display in mobile view to stack the buttons */
.responsiveButtons {
  display: none;
}
